@import url('https://fonts.googleapis.com/css?family=Audiowide');



.endzone-btn {
    width: auto;
    height: 100%;
    font-size: 2.5vh;
    font-weight: 100;
    padding: 1vh;
    padding-left: 3vh;
    padding-right: 3vh;
    border-radius: 2vh;
    color: white;
    border: .05vh solid #0d1019;
    background-color: #353b48;
}

.btn-body {
    width: 100%;
    height: 100%;
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
}

.btn-text {
    width: 100%;
    height: auto;
    border-right: .5vh solid #0877cc;
    margin-right: .5vw;
}

.btn-text p {
    font-weight: 100;
    font-family: "audiowide";
    margin-right: .5vw;
}

.btn-icon {
    margin: 0;
    margin-top: .5vh;
    padding: 0;
}

.endzone-btn:hover {
    transform: scale(1.1);
    transition: .5s ease-in-out;
    cursor: pointer;
}

.endzone-btn:hover p {
    animation: button_hover_shrink 10s;
}

.endzone-btn:hover .btn-icon {
    animation: button_hover_grow 10s;
}

@keyframes button_hover_shrink {
    0% {
        width: 75%;
        font-size: 2vh;
	}


    10% {
        width: 0%;  
        font-size: 0vh;
	}

    90% {
        width: 0%;  
        font-size: 0vh;
	}
}

@keyframes button_hover_grow {
    0% {
        font-size: 100%;
        margin-right: 1.5vw;
	}


    10% {
        font-size: 200%;
	}

    90% {
        font-size: 100%;
	}
}