@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.formation-page {
    width: 100%;
    height: 100%;
}

.formation-body-div {
    width: 100%;
    height: 100%;
}

.formation-table-div {
    position: absolute;
    right: 0;
    width: 33%;
    height: 83vh;
    border: .25vh solid #353b48;
    border-radius: 5%;
    margin: 3vh;
}

.formation-table-header {
    color: white;
    text-align: center;
    padding: 1vh;
    font-size: 2vh;
}

.formation-table-header h4 {
    padding: 1vh;
    border-bottom: .25vh solid #0984e3;
    border-radius: 10%;
}

.formation-table-footer {
    text-align: center;
    margin: 3%;
}

.formation-table-div .table {
    margin-left: 2vh;
    margin-right: 2vh;
    font-size: 2vh;
    background-color: white;
    color: #252830;
    border: .25vh solid #353b48;
    font-family: 'Audiowide';
    height: 75%;
    border-radius: 2.5%;
}

.formation-body {
    display: inline-block;
    width: 64%;
    height: 83vh;
}

.edit-formation {
    display: none;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: auto;
    background-color: #0a0d14;
    border-bottom: .25vh solid #353b48;
    border-right: .25vh solid #353b48;
    padding: .5vh;
}

.input-box {
    color: white;
    font-family: 'Audiowide';
    background-color: #0984e3;
}

.draw-tool {
    width: 100%;
    background-color: grey;
    height: auto;
}

.draw-div {
    display: none;
    height: 90%;
    border: .25vh solid #353b48;
    border-radius: 2.5%;
    margin: 1vh;
}

.view-draw-div {
    overflow-y: auto;
    display: none;
    justify-content: center;
    width: 100%;
    height: 80%;
    background-color: teal;
}

.draw-formation {
    overflow-y: auto;
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: 80%;
}

.drawing-area {
    position: relative;
    width: 700px;
    height: 700px;
    background-color: white;
    margin: 1vh;
}

.drawing-toolbar {
    display: inline-flex;
    justify-content: space-evenly;
    position: relative;
    z-index: 1;;
    width: 100%;
    height: 10vh;
}

.toolbar-action-div {
    display: inline-block;
    text-align: center;
    width: 25%;
    height: 100%;
    font-size: 1.5vh;
    padding: 1vh;
}

.toolbar-action-div h2 {
    width: 100%;
    color: white;
}

.toolbar-actions {
    width: 100%;
    display: inline-flex;
    justify-content: space-evenly;
}

.toolbar-actions button {
    width: 50%;
    font-size: 4vh;
    font-weight: bold;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    border: .1vh solid white;
    margin: 1vh;
    border-radius: 1vh;
    color: #0984e3;
}

.toolbar-actions button:hover {
    transform: scale(1.1);
    transition: .25s ease-in-out;
    background-color: white;
}

.toggle-draw {
    display: none;
    justify-content: center;
    width: 100%;
    height: auto;
}

.logo-div {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.toggle-draw button {
    cursor: pointer;
    color: #0984e3;
    font-family: "Audiowide";
    border: .1vh solid white;
    border-radius: 1vh;
    padding: 1vh;
    margin-top: 2vh;
    font-size: 2vh;
    background-color: rgba(0, 0, 0, 0);
    width: auto;
    height: auto;
}

.toggle-draw button:hover {
    transform: scale(1.1);
    transition: .25s ease-in-out;
    background-color: white;
}

.toggle-draw .btn-div {
    width: 100%;
    display: inline-flex;
    justify-content: center;
}

.cancel-toolbar {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: 5vh;
}

.load-logo {
    width: 50%;
}